* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #f1f5f8;
}
.container {
  max-width: 1000px;
  margin: auto;
  text-align: center;
  margin-top: 10vh;
  padding: 15px;
}
.header h1 {
  font-weight: 600;
  letter-spacing: 5px;
}
.header span {
  color: rgb(197, 98, 56);
  margin-right: 15px;
}
/* ///////////// */

.sliderContainer {
  width: 100%;
  height: 550px;
  position: relative;
  text-align: center;
  display: flex;
  overflow: hidden;
}

.imageContainer {
  width: 150px;
  height: 150px;
  margin: auto;
  margin-top: 50px;
  border-radius: 50%;
  box-shadow: gray 2px 2px 5px;
  background-color: rgb(148, 68, 54);
  padding: 3px;
}
.imageContainer img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}
.detailscontainer {
  width: 100%;
  margin: auto;
  text-align: center;
  line-height: 25px;
  margin-top: 20px;
}
.detailscontainer h3 {
  color: tomato;
  letter-spacing: 3px;
}
.detailscontainer h4 {
  color: rgb(43, 36, 36);
  letter-spacing: 2px;
}
.info {
  width: 80%;
  margin: auto;
  text-align: center;
  line-height: 30px;
  margin-top: 15px;
}
.info p {
  color: rgb(88, 86, 86);
  font-weight: 600;
}
.qiotes {
  width: 100%;
  margin: auto;
  text-align: center;
}
.quet {
  font-size: 50px;
  color: tomato;
  margin-top: 10px;
}
.sliderbuttons {
  display: flex;
  position: absolute;
  justify-content: space-between;
  width: 100%;
  top: 220px;
}
.btn {
  font-size: 25px;
  color: tomato;
  border-radius: 5px;
  padding: 2px;
  background-color: gray;
  border-color: transparent;
  line-height: 5px;
  outline-color: tomato;
}
.oneSlider {
  position: absolute;
  opacity: 0;
  transition: 0.25s ease-in-out;
  /* overflow: hidden; */
}
.activeSlider {
  opacity: 1;
  transform: translateX(0);
}
.lastSlider {
  transform: translateX(-100%);
}
.nextSlider {
  transform: translateX(100%);
}
